@media screen and (max-width: 767px) {
  .nav__link {
    width: 100%;
    height: 100%;
    padding: 16px;
    display: block;
  }

  .nav__link--bgc-green:hover, .nav__link--bgc-green:focus {
    background-color: #bbf330;
  }
}

@media screen and (min-width: 768px) {
  .nav__link {
    text-shadow: 0 0 15px rgba(105, 105, 105, .1);
    padding: 28px 0;
    transition: all .3s cubic-bezier(.075, .82, .165, 1);
    display: block;
    position: relative;
  }

  .nav__link:after, .nav__link:before {
    content: "";
    width: 0%;
    height: 2px;
    margin-top: 2px;
    transition: all .3s ease-in;
    display: block;
    position: absolute;
  }

  .nav__link:after {
    bottom: 25%;
    left: 0;
  }

  .nav__link:before {
    top: 25%;
    right: 0;
  }

  .nav__link:hover:after {
    width: 100%;
    height: 2px;
    background-color: #bbf330;
    bottom: 25%;
  }

  .nav__link:hover:before {
    width: 100%;
    height: 2px;
    background-color: #bbf330;
    top: 25%;
  }
}

